function Card({ info, wallet }) {
  const [timeText, setTimeText] = useState("")
  const [isCheckIn, setIsCheckIn] = useState(false)
  const [isDrop, setIsDrop] = useState(false)
  const [isStart, setIsStrat] = useState(info.isStart)

  const getIsCheckIn = useCallback(async () => {
    let path = "https://anniversary.deri.io/checkin"
    let res = await axios.get(path, {
      params: {
        address: wallet.detail.account
      }
    })
    console.log(res.data)
    if (res.data) {
      setIsCheckIn(res.data.result.status)
      if (res.data.result.address.toLocaleLowerCase() === wallet.detail.account.toLocaleLowerCase()) {
        const now = new Date();
        const utc10 = new Date(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(), 10);
        // å°† UTC æ—¶é—´çš„ 10 ç‚¹è½¬æ¢æˆæ—¶é—´æˆ³
        const utc10timestamp = utc10.getTime();

        // è·å–å‰ä¸€å¤©çš„æ—¥æœŸ
        const yesterday = new Date(now - 24 * 60 * 60 * 1000);
        const yesterdayYear = yesterday.getFullYear();
        const yesterdayMonth = yesterday.getMonth() + 1;
        const yesterdayDate = yesterday.getDate();

        // å°†æ—¶é—´æˆ³è½¬æ¢æˆæ—¥æœŸå¯¹è±¡
        const timestamp = res.data.result.timestamp * 1000;
        const date = new Date(timestamp);
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        if (year === yesterdayYear && month === yesterdayMonth && day <= yesterdayDate) {
          if (utc10timestamp < now.getTime()) {
            setIsDrop(true)
          }
        }
      }

    }
  }, [wallet])

  const checkIn = useCallback(async () => {
    let path = `https://anniversary.deri.io/checkin?address=${wallet.detail.account}`
    let res = await axios.post(path)
    console.log(res)
    if (res.data && res.data.result.address) {
      let bool = res.data.result.address.toLocaleLowerCase() === wallet.detail.account.toLocaleLowerCase()
      if (bool) {
        setIsCheckIn(bool)
      }
    }
  }, [wallet.detail.account])


  const click = useCallback(async () => {
    if (!isDrop) {
      info.link && window.open(info.link)
    } else if (isDrop && wallet.detail.account) {
      if (!info.isWallet) {
        info.link && window.open(info.link)
      } else {
        window.open("https://galxe.com/DeriProtocol/campaign/GChLLU4kcC")
      }
    }
  }, [info, wallet.detail.account, isCheckIn, isDrop])

  useEffect(() => {
    if (wallet.detail.account && info.isWallet) {
      getIsCheckIn()
      checkIn()
    }
  }, [wallet.detail.account])

  useEffect(() => {
    let outTime;
    if (info && info.time) {
      outTime = setInterval(() => {
        let time = new Date()
        let timestamp = (time.getTime()) / 1000
        let lefttime = info.time - timestamp;
        var d = parseInt(lefttime / (24 * 60 * 60))
        var h = parseInt(lefttime / (60 * 60) % 24);
        var m = parseInt(lefttime / 60 % 60);
        if (d < 0 || h < 0 || m < 0) {
          setIsStrat(false)
          return false
        }
        let text = `${d}D : ${h}H : ${m}M`
        setTimeText(text)
      }, 1000);
    }
    return () => {
      outTime && clearInterval(outTime)
    }
  }, [timeText]);
  return (
    <div className={classNames("activity-card-info", { "isStart-activity": !isStart })}>
      {!isStart && <div className='has-not-started'>

      </div>}
      <div className='activity-card-info-box'>
        <div className='activity-card-info-box-header'>
          <div className={classNames({ 'color-text': isStart })}>
            {info.id}
          </div>
          {isStart && <div className='timeout'>
            {timeText}
          </div>}
          {!isStart && info.time && <div className='timeout'>
            END
          </div>}
        </div>
        <div className='activity-card-info-box-img'>
          <img src={info.img} alt={info.id} />
        </div>
        <div className='activity-card-info-box-text'>
          {info.text}
        </div>
        <div className='activity-card-info-box-button'>
          <Button className="button-more" onClick={click} label={(info.isWallet && isDrop) ? "LUCKY DRAW" : info.buttonText} />
        </div>
      </div>
    </div>
  )
}
export default inject('wallet', 'intl')(observer(Anniversary))